<template>
  <div class="send-message">
    <Button class="btn-back" type="warning" @click="back">返回</Button>
    <Button class="btn-clear" @click="clearList">清空列表</Button>
    <Table class="message-table" stripe :columns="messageHeader" :data="messageData"></Table>
    <div class="tag-modal" v-if="tagModal">
      <h3>请选择要发送的标签</h3>
      <br>
      <RadioGroup v-model="tagId">
        <Radio v-for="(tag, index) in tagList" :key="index" :label="tag.id">{{tag.name}}</Radio>
      </RadioGroup>
      <br>
      <Button class="btn" type="warning" @click="tagModalClose">取消</Button>
      <Button class="btn" type="success" @click="tagModalSubmit">确定</Button>
    </div>
     <div class="tag-modal index-modal" v-if="indexModal">
      <h3>请输入要删除的消息的索引</h3><br>
      <span style="color: red;font-size:12px;">Tips:&nbsp;&nbsp;&nbsp;0或者不填为全部删除<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;1、2、3...为对应第几条</span>
      <br>
      <br>
      <Input type="text" v-model="article_idx" placeholder="您要删除第几条消息？" />
      <Button class="btn" type="warning" @click="indexModalClose">取消</Button>
      <Button class="btn" type="success" @click="indexModalSubmit">确定</Button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tagModal: false,
      indexModal: false,
      tagList: [],
      id: "",
      tagId: 0,
      msg_id: '',
      article_idx: '',
      mediaId: "",
      messageData: [],
      messageHeader: [
        {
          key: "media_id",
          title: "media_id",
          align: "center",
          width: 100
        },
        {
          key: "name",
          title: "name",
          align: "center"
        },
        {
          key: "url",
          title: "url",
          align: "center",
          width: 80
        },
        {
          key: "update_time",
          title: "update_time",
          align: "center",
          render: (h, params) => {
            let time = this.formatDate(params.row.update_time)
            return h('span', {
              props: {}
            }, time)
          }
        },
        {
          title: "图文标题",
          align: "center",
          render: (h, params) => {
            let arr = params.row.content.news_item.length != 0 ? params.row.content.news_item : [];
            let titles = [];
            for(let i = 0; i < arr.length; i ++) {
              titles.push(i + 1 + '.' +arr[i].title)
            }
            return h(
              "p",
              {
                style: {
                  textAlign: 'left',
                  padding: '5px'
                }
              },
              titles
            );
          }
        },
        {
          title: "Action",
          align: "center",
          render: (h, params) => {
            let that = this;
            return h("div", [
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.tagModal = true;
                      that.id = params.row._id;
                      that.mediaId = params.row.mediaId;
                    }
                  }
                },
                "发送消息"
              ), h(
                "Button",
                {
                  props: {
                    type: "warning",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.indexModal = true;
                      this.msg_id = params.row.msg_id
                    }
                  }
                },
                "删除消息"
              )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    this.showDataList();
    this.getTagList();
  },
  methods: {
    back() {
      this.$router.push({
        name: "群发列表"
      });
    },
    clearList() {
      this.$axios.get('/material/clear', {params: {code: this.$route.query.code}})
      .then(res => {
        this.$Message.info(res.data.success)
        this.showDataList()
      })
    },
    showDataList() {
      this.$axios
        .get("/material/show", { params: { code: this.$route.query.code } })
        .then(res => {
          this.messageData = res.data.data;
        });
    },
    getTagList() {
      this.$axios.get("/material/tag", {params: {code: this.$route.query.code}}).then(res => {
        this.tagList = res.data.data;
      });
    },
    tagModalClose() {
      this.tagModal = false;
    },
    tagModalSubmit() {
      this.$axios
        .get("/material/sendMsg", {
          params: {
            id: this.id,
            tagId: this.tagId,
            mediaId: this.mediaId
          }
        })
        .then(res => {
          this.$Message.info(res.data.success);
          this.tagModal = false;
        });
    },
    indexModalClose() {
      this.indexModal = false;
    },
    indexModalSubmit() {
      this.$axios.get('/material/del_msg', {
        params: {
          code: this.$route.query.code,
          msg_id: this.msg_id,
          article_idx: this.article_idx || ''
        }
      }).then(res => {
        this.$Message.info(res.data.success);
        this.indexModal = false;
      })
    },
    formatDate(time) {
      var date = new Date(Number(time)*1000);
      var year = date.getFullYear(),
        month = date.getMonth() + 1,
        day = date.getDate(),
        hour = date.getHours(),
        min = date.getMinutes(),
        sec = date.getSeconds();
      var newTime =
        year +
        "-" +
        (month < 10 ? "0" + month : month) +
        "-" +
        (day < 10 ? "0" + day : day) +
        " " +
        (hour < 10 ? "0" + hour : hour) +
        ":" +
        (min < 10 ? "0" + min : min) +
        ":" +
        (sec < 10 ? "0" + sec : sec);
      return newTime;
    }
  }
};
</script>

<style scoped>
.message-table {
  width: 100%;
  clear: both;
}
.tag-modal {
  width: 300px;
  /* height: 180px; */
  padding: 30px;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-left: -165px;
  margin-top: -105px;
  background: #eee;
  z-index: 100000;
  color: #999;
  text-align: left;
}
.tag-modal .btn {
  margin-top: 30px;
  margin-right: 20px;
}

.btn-back {
  margin-bottom: 30px;
  float: left;
}
.btn-clear {
  float: right;
}
</style>

